<template>
  <div class="search_box">
    <div class="search_center_box">
      <p class="search_title">Check out token balance</p>
      <div class="search_input">
        <a-input-search
            v-model:value="maxSearchValue"
            placeholder=""
            style="width: 640px"
            size="large"
            allowClear
            @search="onMaxSearch"
        />
      </div>
      <p class="search_text">Recognize all operations, deploy mint transfer.</p>
      <div class="owner_center_box">
        <a-radio-group v-model:value="radioValue" size="large" @change="onMaxSearch">
          <a-radio-button value="tokens">Tokens</a-radio-button>
          <a-radio-button value="scripts">Scripts</a-radio-button>
        </a-radio-group>
        <div class="owner_refresh">
          <p>Refresh</p>
          <a-button type="text" shape="circle" size="large" @click="onMaxSearch">
            <img src="@/assets/img/result.svg" alt="">
          </a-button>
        </div>
      </div>
      <a-list v-show="radioValue === 'tokens'" :grid="{ gutter: 5, xs: 1, sm: 2, md: 4, lg: 4, xl: 4, xxl: 4 }" :data-source="data" style="margin-left: -8px; margin-right: -8px; margin-top: 30px;">
        <template #renderItem="{ item }">
          <a-list-item style="padding: 0 8px">
            <a-card style="overflow: hidden">
              <template #cover>
                <div class="list_item_top">
                  <p class="item_top_title">{{item.name}}</p>
                  <div class="item_top_text">
                    <span>{{ item.quantity }}</span>
                    <p>{{ item.mints+' mints' }}</p>
                  </div>
                </div>
                <div class="list_item_bottom">
                  <p class="item_bottom_title">{{ '#'+item.id }}</p>
                </div>
              </template>
            </a-card>
          </a-list-item>
        </template>
      </a-list>
      <a-list v-show="radioValue === 'scripts'" :grid="{ gutter: 5, xs: 1, sm: 2, md: 4, lg: 4, xl: 4, xxl: 4 }" :data-source="data1" style="margin-left: -8px; margin-right: -8px; margin-top:30px;">
        <template #renderItem="{ item }">
          <a-list-item style="padding: 0 8px">
            <a-card style="overflow: hidden">
              <template #cover>
               <div class="list_item_scroll">
                  {
                  <pre class="item_top_json">
                      &nbsp;&nbsp;"p":"script",
                      &nbsp;&nbsp;"op":"{{item.op}}",
                      &nbsp;&nbsp;"tick":"{{item.tick}}",
                      &nbsp;&nbsp;"amt":"{{item.amt}}"
                  </pre>
                  }
                </div>
                <div class="list_item_bottom">
                  <div class="item_bottom_btn">
                    <p>{{ '#'+item.id }}</p>
                    <p>{{ item.address.substr(0, 5)+"..."+item.address.substr(item.address.length - 5) }}</p>
                  </div>
                  <p class="item_bottom_text">{{ dayjs(item.date).format('YYYY-MM-DD HH:mm:ss') }}</p>
                </div>
              </template>
            </a-card>
          </a-list-item>
        </template>
      </a-list>
    </div>
  </div>
</template>

<script>
import {defineComponent, reactive, toRefs,onMounted} from "vue";
import { useRouter, useRoute, onBeforeRouteLeave } from "vue-router";
import {getAddressScripts,getAddressTokens} from '@/utils/api'
import {  notification } from 'ant-design-vue';

export default defineComponent({
  name: "SearchView",
  setup() {
    const route = useRoute();
    let dayjs = require('dayjs')
    const Data = reactive({
      maxSearchValue: route.query.address,
      radioValue: 'tokens',
      data: [],
      data1: [],
    });

    onMounted(() => {
      Methods.loadData();
    });

    const Methods = {
      onMaxSearch() {
        Methods.loadData();
      },
      loadData(){
        if(Data.maxSearchValue=="")return
        Data.page=1
        Data.tableLoading=true
        if(Data.radioValue=="tokens"){
          getAddressTokens(Data.maxSearchValue).then((response) => {
            Data.tableLoading=false
            if(response.code==0){
              Data.data = response.data
            }else{
              Data.tableLoading=false
              notification["error"]({message:"Error",description:response.msg});
            }
          })
        }else{
          getAddressScripts(Data.maxSearchValue).then((response) => {
            Data.tableLoading=false
            if(response.code==0){
              Data.data1 = response.data
            }else{
              Data.tableLoading=false
              notification["error"]({message:"Error",description:response.msg});
            }
          })
        }
      }
    }
    return {
      ...toRefs(Data),
      ...Methods,
      dayjs
    }
  }
})
</script>

